import React, { useEffect, useState } from 'react';
import FilerobotImageEditor from 'filerobot-image-editor';
import Helmet from 'react-helmet';
import SubNavBar from '../../Components/SubNavBar';
import { Card, Container, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
    ulElem: {
        listStyle: 'disc !important',
        padding: '1.5rem',
        borderBottom: 'solid',
        borderTop: 'solid',
        "& li": {
            listStyleType: 'disc'
        }
    }
}));

export default function Imageeditor(props) {
    const classes = useStyles();
    const [show, toggle] = useState(false);
    const [file, setFile] = useState();

    const onCHangeFile = (e) => {
        if (e.target.files.length > 0) {
            setFile(e.target.files[0]);
            toggle(true);
        }
    }

    useEffect(() => {
        let namediv1 = document.getElementsByClassName('sc-fznMAR');
        let namediv2 = document.getElementsByClassName('fgCecj');
        let namediv3 = document.getElementsByClassName('oYzuD');
        if (namediv1.length > 0) {
            namediv1[0].style.display = 'none';
            namediv1[0].innerText = '';
        }
        if (namediv2.length > 0) {
            namediv2[0].style.display = 'none';
            namediv2[0].innerText = '';
        }
        if (namediv3.length > 0) {
            namediv3[0].style.display = 'none';
            namediv3[0].innerText = '';
        }

        var filerbot = document.getElementById("filerobot-image-editor-root");
        if (filerbot) {
            filerbot.style.position = "absolute";
        }

    }, [show, toggle]);

    return (

        <>  <Container maxWidth="xl">
            <Helmet>
                <title>Free Online Image Editor | Mathcalc</title>
                <meta name="keywords" content="free online image editor gif edit animated photo photos pictures gifs jpeg jpg bmp convert converter create creator animate png tiff picture resize crop split frames text to add mask" />
                <meta name="description" content="Mathcal Free Online Image Editor tool create your own images resize crop avatars and images. Photo tool for your favorite pictures. Edit an image here fast and easy online." />
                <meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1"></meta>
            </Helmet>
            {!show ?
                <>
                    <div className="container" style={{ padding: '1rem' }} data-v-14591542>
                        <SubNavBar
                            pageTitle="Online image editor"
                            links={[{
                                url: "/tools/",
                                urlName: "Tools"
                            }]}
                            txtTitle="Mathcal Free Online Image Editor tool create your own images resize crop avatars and images. Photo tool for your favorite pictures. Edit an image here fast and easy online."
                        />
                        <section className="hero" data-v-23847e07>
                            <div style={{ padding: '1rem 0.5rem 0rem 0.5rem' }}>
                                <div className="container">
                                    <br />
                                    <h1 style={{ display: 'flex' }} className="subtitle is-spaced is-uppercase has-text-weight-bold">
                                        <div class="sc-1gyxcpm-0 csDfHB sc-17y9jfw-4 iqUd" style={{ width: '32px', height: '32px' }}>
                                            <img src="https://visualpharm.com/assets/548/Edit%20Image-595b40b85ba036ed117dc0a5.svg" alt="edit im " width="32" height="32" />
                                        </div>
                                        <div>
                                            &nbsp;Free Online Image Editor
                                        </div>
                                    </h1>
                                    <br />
                                    <div className="columns" data-v-14591542>
                                        <div className="column is-8" style={{
                                            backgroundColor: 'rgb(255, 183, 0)',
                                            border: 'none',
                                            padding: '2rem',
                                        }} data-v-14591542>
                                            <div data-v-14591542 style={{ border: '1px dashed rgba(0, 0, 0, 0.15)' }}>
                                                <div className="file-dropzone" style={{ backgroundColor: '#e5a400', border: 'none' }} data-v-14591542>
                                                    <input id="image" type="file" data-v-14591542 onChange={onCHangeFile}
                                                        accept="image/gif, image/jpeg, image/png"
                                                    />
                                                    <p className="has-text-centered has-text-3 is-vcentered" style={{ display: 'contents' }} data-v-14591542>
                                                        <span data-v-14591542>
                                                            <img style={{ color: 'white' }} src="https://visualpharm.com/assets/544/Image%20File-595b40b75ba036ed117d7045.svg" alt="edit" width="100px"></img>
                                                        </span>
                                                        <span className="is-3" style={{ color: 'white' }} data-v-14591542>Drag and drop your file here or click here to upload.</span>
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="column is-4" data-v-14591542>
                                            <div className="is-pulled-right" data-v-14591542>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                        <br />
                        <br />
                        <Grid>
                            <Grid item sm={12} md={8} lg={8}>
                                <Card elevation={1} className={" box"}>
                                    <h1 className="title is-5">
                                        The Free Online Image Editor lets you edit images ONLINE!</h1>
                                    <ul className={classes.ulElem} >
                                        <li>
                                            Resize or Crop all images.
                                </li>
                                        <li>
                                            Merge, Blend and Overlay Images with the editor.
                                </li>
                                        <li>
                                            Add Text with your own fonts to an  image.
                                </li>
                                        <li>
                                            Add Borders, round corners and shadow to your photo.
                                </li>
                                        <li>
                                            Put an image in a Photo Frame or add a Mask.
                                </li>
                                        <li>
                                            Convert, sharpen, reduce size of an image...
                                </li>
                                        <li>
                                            Use the Cut Out Tool to make a custome shape image.
                                </li>
                                    </ul>
                                    <br />
                                    <p>Absolutely for free !</p>
                                    <br />
                                </Card>

                            </Grid>
                            <Grid item sm={12} md={4} lg={4}></Grid>
                        </Grid>
                        <br />
                    </div>
                </> : <></>
            }
        </Container>
            <FilerobotImageEditor
                show={show}
                src={file}
                onClose={() => { toggle(false) }}
                onOpen={() => {
                    let namediv = document.getElementsByClassName('sc-fznMAR');
                    if (namediv.length > 0) {
                        namediv[0].style.display = 'none';
                    }
                }}
                config={
                    {
                        showInModal: true,
                        isLowQualityPreview: true,
                    }
                }
            />
        </>

    )
};

