import React from 'react';
import { Chart, registerables } from 'chart.js';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Container, TextField } from '@material-ui/core';
import SubNavBar from '../../../Components/SubNavBar';
import Helmet from 'react-helmet';

const math = require('mathjs');

Chart.register(...registerables);

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        overflow: 'hidden'
    },
    control: {
        padding: theme.spacing(2),
    },
    divGraph: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        maxWidth: '600px',
        maxHeight: '600px',
        marginTop: '5px',
        minWidth: '330px',
        minHeight: '330px',
    },
    divCard: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        padding: '2rem',
        cursor: 'default !important',
        margin: '10px !important'
    }
}));

export default function Graphs() {

    const [state, SetState] = React.useState({
        equation: '(x)^2',
        limitX: 5,
        limitY: 10,
        objChart: undefined,
    });

    const createGraph = (expression) => {
        try {
            let xArray = [];
            let yArray = [];
            xArray = Array.from({ length: parseInt(state.limitX) + 1 }, (_, i) => i);
            let negvalues = Array.from({ length: parseInt(state.limitX) + 1 }, (_, i) => -i);
            for (let i = 0; i < negvalues.length; ++i) {
                xArray.push(negvalues[i]);
            }
            xArray.sort(function (a, b) { return (a - b) })
            let strEquation = expression;
            let yval = 0;
            let strEval = '';
            console.log(strEquation);
            for (let i = 0; i < xArray.length; ++i) {
                strEval = strEquation.replaceAll('x', xArray[i])
                console.log(strEval);
                yval = math.evaluate(strEval);
                yArray.push(yval);
            }

            let ctx = document.getElementById('myChart').getContext('2d');
            // if (chart) {
            //     chart.destroy();
            // }

            if (
                window.myLine !== undefined
                &&
                window.myLine !== null
            ) {
                window.myLine.destroy();
            }

            let data = {
                labels: xArray,
                // Information about the dataset
                datasets: [{
                    label: "Y Axis",
                    backgroundColor: 'lightblue',
                    borderColor: 'royalblue',
                    data: yArray,
                }]
            }

            window.myLine = new Chart(ctx, {
                // The type of chart we want to create
                type: 'line', // also try bar or other graph types
                // The data for our dataset
                data: data,
                // Configuration options
                options: {
                    responsive: true,
                    plugins: {
                        legend: false,
                        title: {
                            display: true,
                            text: 'Visualize algebric equation'
                        },
                    },
                    interaction: {
                        intersect: false
                    },
                    scales: {
                        x: {
                            type: 'linear',
                            display: true,
                            title: {
                                display: true,
                                text: 'X - Axis'
                            }
                        },
                        y: {
                            display: true,
                            title: {
                                display: true,
                                text: 'Y - Axis'
                            }
                        }
                    }
                },
            })

        } catch (e) {

        }

    }

    const [first, setFirst] = React.useState(true);

    React.useEffect(() => {
        let mathfield = document.querySelector('math-field');
        mathfield.addEventListener('input', (ev) => {
            try {
                createGraph(ev.target.value);
                // SetState({
                //     ...state,
                //     equation: ev.target.value
                // })
            } catch (e) { }
        });

        if (first) {
            createGraph('(x)^2');
            setFirst(false);
        }

        // eslint-disable-next-line
    }, [state.limitX]);
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <Helmet>
                <meta name="viewport" content="width=device-width, initial-scale=0.6" />
                <title>Graphs - Visualize the equations in graphs | MathCalc</title>
                <meta name="keywords" content="Mathcalc, online graph calculator, visualize equation, visualize algebric equation" />
                <meta name="description" content="online free graph calculator can calculate your mathematical algebric expression into the visible graph format" />
                <meta name="author" content="Mathcalc" />
                <meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1"></meta>
                <script src="https://unpkg.com/mathlive/dist/mathlive.min.js"></script>
            </Helmet>
            <Container maxWidth={'xl'} >
                <Grid container direction="row" justify="center" alignItems="center">
                    <Grid item lg={8} md={8} sm={12}>
                        <SubNavBar
                            pageTitle="Visualize equations in graphs"
                            links={[{
                                url: "/maths/",
                                urlName: "Mathematics"
                            }]}
                            txtTitle="Evaluate the equation and view in the cartesian plane"
                        />
                        <br />
                        <div className={'div-card ' + classes.divCard}>
                            <Grid container spacing={2}>
                                <Grid item sm={6} md={6} lg={6}>
                                    <div style={{ display: 'flex', alignItems: 'center' }}>
                                        <p>y &nbsp;= &nbsp; </p>
                                        <math-field
                                            style={{
                                                minWidth: '243px',
                                                border: '1px solid',
                                                borderRadius: '4px',
                                                padding: '6px',
                                                margin: '10px',
                                            }}
                                        >x^2</math-field>
                                    </div>
                                </Grid>
                                <Grid item sm={6} md={6} lg={6}>
                                    <TextField id="limitX" value={state.limitX} type={'number'}
                                        onChange={(e) => {
                                            SetState({ ...state, limitX: e.target.value })
                                            createGraph(state.equation);
                                        }
                                        }
                                        label={"Limit of x value"}></TextField>
                                </Grid>
                            </Grid>
                        </div>
                        <div className={'div-card ' + classes.divCard}>
                            <canvas id="myChart" className={classes.divGraph} width={330} height={330}></canvas>
                        </div>
                    </Grid>
                    <Grid item lg={4} md={4} sm={false}></Grid>
                </Grid>
            </Container>
        </div >
    );
}
